import { categoryConstants  } from '../constants/category.constant'
const initialState = {
  isFetching: false,
  categoryId: '',
  payload: {
    categories: [],
    product: []
  },
}
export default function category(state = initialState, action) {

    switch (action.type) {
      case categoryConstants.CATEGORY_REQUEST:
        return {
            isFetching: true,
            categoryId: action.category,
            payload: {
              categories: [],
              product: []
            },
        };
      case categoryConstants.CATEGORY_SUCCESS:
        return {
          isFetching: false,
          payload: action.payload
        };
      case categoryConstants.CATEGORY_FAILURE:
        return {};
     
      default:
        return state
    }
  };