import React, { useState, useEffect } from 'react';
import DropDownItem from '../container/DropDownContainer';
import '../sass/DropDown.scss';
import styled from "styled-components";
const DropDownButton = styled.div`
    display: inline-flex;
    position: relative;
    overflow: visible;
    padding: 0px 8px;
`;

const DropDownGroup = styled.div`
position: absolute;
top: ${props => props.top || '48px'};
left: 0px;
box-shadow: rgba(78, 89, 93, 0.15) 0px 8px 11px 0px;
z-index: 10;
background: rgb(255, 255, 255);
padding: 9px 0px;
`
const Label = styled.label`
margin-right: 8px;
margin-bottom: 0px;
font-weight: 400;
`
const ButtonItem = styled.div`
    &:active {
        background: rgba(78,89,93,.3);
    }
    &:after {
        display: inline-block;
    width: 0;
    height: 0;
    margin-left: 0.3em;
    vertical-align: middle;
    content: "";
    border-top: 0.3em solid;
    border-right: 0.3em solid transparent;
    border-left: 0.3em solid transparent;
    }
    ${props => props.addStyle || null }
`
export default class DropDown extends React.Component {
    constructor(props) {
        super();

        this.state = {
            showMenu: false,
        };
   

    }
    showMenu = (event) => {
        event.preventDefault();
        this.setState({ showMenu: true }, () => {
            document.addEventListener('click', this.closeMenu);
        });
    }

    closeMenu = () => {
        this.setState({ showMenu: false }, () => {
            document.removeEventListener('click', this.closeMenu);
        });
    }
    render() {
        const {  selectedOption, label } = this.props;
        const Content = this.state.showMenu ? <DropDownContent /> : null;
        return (
            <div>
                 <Label>
                    { label? label : null}
                </Label>
               <DropDownButton  onClick={this.showMenu} > 
                <div >
                        <ButtonItem> { selectedOption ? selectedOption.label : null}</ButtonItem>
                </div>           
                {Content}
            </DropDownButton>
            </div>
        )
    }
}
function DropDownContent(props) {
    const options = [{"label":"Gi ","name":"SHOW_ALL"},{"label":"Gim gi nhiu nht","name":"HIGHEST_DISCOUNT"},{"label":"Gi, thp n cao","name":"LOW_PRICE"},{"label":"Gi, cao n thp","name":"HIGH_PRICE"}];


    return (
        <DropDownGroup top="28px">

            {options.map(e => ( <DropDownItem filter={e.name}> {e.label} </DropDownItem>))}
       </DropDownGroup>
    )
}